import React, {useState, useEffect} from 'react';
import { Link } from 'react-router-dom';
import { Button } from './Button';
import './Navbar.css';

function Navbar() {
    const [click, setClick] =  useState(false);
    const [button, setButton] = useState(true);

    const handleClick = () => setClick(!click);
    const closeMobileMenu = () => setClick(false);

    const showButton = () => {
        if(window.innerWidth <= 960) {
            setButton(false);
        }
        else{
            setButton(true);
        }
    }

    useEffect(() => {
        showButton()
    }, []);

    window.addEventListener('resize', showButton); 



  return (
  <>
        <nav className = "navbar">
            <div className="navbar-container">
                <Link to="/" className = "navbar-logo" onClick={closeMobileMenu}>UčeničkiWebServis <i className="fab fa-typo3"></i></Link>
                <div className="menu-icon" onClick={handleClick}>
                    <i className={click ? 'fas fa-times' : 'fas fa-bars' } />
                </div>
                <ul className={click ? 'nav-menu active' : 'nav-menu'}>
                    <li className='nav-item'>
                        <Link to = '/' className='nav-links' onClick={closeMobileMenu}>
                            Početna
                        </Link>
                    </li>
                    <li>
                        <Link to = '/o-nama' className='nav-links' onClick={closeMobileMenu}>
                            O Nama
                        </Link>
                    </li>
                    <li>
                        <Link to = '/poslovi' className='nav-links' onClick={closeMobileMenu}>
                            Pretraga Poslova
                        </Link>
                    </li>
                        <li>
                    <Link to = '/prijava' className='nav-links-mobile' onClick={closeMobileMenu}>
                            Prijava
                        </Link>
                    </li>
                </ul>
                {button && <Button buttonStyle={'btn--outline'}>Prijava</Button>}
            </div>
        </nav>
  </>
  ); 
}

export default Navbar;
 